raul-radio {
  display: inline-block;
  vertical-align: middle;
  font-size: 0.875rem;
}

raul-radio .r-radio__label {
  cursor: pointer;
  display: flex;
  align-items: center;
  position: relative;
}

raul-radio .r-radio__label input[type=radio] {
  position: absolute;
  height: 0;
  opacity: 0;
  width: 0;
  z-index: -10;
}

raul-radio .r-radio__label input[type=radio]:checked ~ .r-radio__label__icon {
  background-color: #0076CC;
  border-color: #0076CC;
}

raul-radio .r-radio__label input[type=radio]:checked ~ .r-radio__label__icon::after {
  position: absolute;
  background-color: #fff;
  left: 50%;
  border-radius: 9999px;
  top: 50%;
  content: "";
  height: 6px;
  transform: translate(-50%, -50%);
  width: 6px;
}

raul-radio .r-radio__label input[type=radio]:disabled ~ .r-radio__label__icon {
  background-color: #EBEDEE;
  border-color: #EBEDEE;
}

raul-radio .r-radio__label input[type=radio]:disabled:checked ~ .r-radio__label__icon::after {
  background-color: #9BA3A7;
}

body[modality] raul-radio .r-radio__label input[type=radio]:focus ~ .r-radio__label__icon::before {
  position: absolute;
  border-radius: 0.125rem;
  top: -4px;
  right: -4px;
  bottom: -4px;
  left: -4px;
  box-shadow: 0 0 0 1px #0076CC;
}

raul-radio .r-radio__label__icon {
  background-color: #fff;
  border-width: 1px;
  border-color: #C6CCD0;
  flex: none;
  position: relative;
  border-radius: 9999px;
  height: 20px;
  transition: background-color 0.15s linear, border-color 0.15s linear;
  width: 20px;
}

raul-radio .r-radio__label__text {
  font-size: 0.875rem;
  margin-left: 0.5rem;
  word-break: break-all;
}

raul-radio .r-radio--small .r-radio__label__icon {
  height: 14px;
  width: 14px;
}

raul-radio .r-radio--small .r-radio__label input[type=radio]:checked ~ .r-radio__label__icon::after {
  height: 4px;
  width: 4px;
}

raul-radio .r-radio--invalid .r-radio__label__icon {
  border-width: 1px;
  border-color: #D01A1F;
}